export interface InitAssetManager extends BaseAction {
  type: ActionConstants.INIT_ASSET_MANAGER;
  payload: {
    systemKey: string;
    deploymentId: string;
  };
}

export const createInitAssetManager = (
  systemKey: string,
  deploymentId: string,
): InitAssetManager => ({
  type: ActionConstants.INIT_ASSET_MANAGER,
  payload: {
    systemKey,
    deploymentId,
  },
});

