import React, { SFC } from 'react';
import Helmet from 'react-helmet';

const DeployHelmet: SFC<{}> = (props) => {
  return (
    <Helmet
      title="ClearBlade - Deploy"
      meta={[{ name: 'description', content: 'Description of Deploy' }]}
    />
  );
};

export default DeployHelmet;



