/**
 *
 * TableSyncInfo
 *
 */

import React, { SFC } from 'react';
// import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import messages from './messages';

const TableSyncInfo: SFC<{}> = () => {
  return (
    <dl>
      <dt>
        <FormattedMessage {...messages.dontDeploy} />
      </dt>
      <dd>
        <FormattedMessage {...messages.dontDeployInfo} />
      </dd>
      <dt>
        <FormattedMessage {...messages.deployTable} />
      </dt>
      <dd>
        <FormattedMessage {...messages.deployTableInfo} />
      </dd>
      <dt>
        <FormattedMessage {...messages.syncTable} />
      </dt>
      <dd>
        <FormattedMessage {...messages.syncTableInfo} />
      </dd>
      <dt>
        <FormattedMessage {...messages.item} />
      </dt>
      <dd>
        <FormattedMessage {...messages.itemInfo} />
      </dd>
    </dl>
  );
};

export default TableSyncInfo;



