/*
 *
 * DeploymentAssetManager actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './actionConstants';

export interface ToggleExpandAll extends BaseAction {
  type: ActionConstants.TOGGLE_EXPAND_ALL;
}

export const createToggleExpandAll = (): ToggleExpandAll => ({
  type: ActionConstants.TOGGLE_EXPAND_ALL,
});

export interface ToggleExpandAssetClass extends BaseAction {
  type: ActionConstants.TOGGLE_EXPAND_ASSET_CLASS;
  payload: {
    assetClass: string;
  };
}

export const createToggleExpandAssetClass = (
  assetClass: string,
): ToggleExpandAssetClass => ({
  type: ActionConstants.TOGGLE_EXPAND_ASSET_CLASS,
  payload: {
    assetClass,
  },
});

export type ActionTypes = ToggleExpandAll | ToggleExpandAssetClass;



