/**
 *
 * DeploymentTableSyncStrategy
 *
 */

import React from 'react';
import styled from 'styled-components';

import { ButtonGroup, Button } from 'reactstrap';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';

import WrenchIcon from 'react-icons/lib/fa/wrench';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { SyncStrategies } from 'cb-utils/console-entity-models/deployment';
import SyncStrategyInfo from 'components/SyncStrategyInfo';
import TableSyncInfo from 'components/TableSyncInfo';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { EntityModels } from 'containers/Console/state';
import { detailUrls } from 'cb-utils/getUrls';

const ButtonGroupWrapper = styled(ButtonGroup)`
  button {
    border: 1px solid !important; /* make sure that our buttons have a border as the carousel in the deployment page removes button borders */
  }
`;

interface DeploymentTableSyncStrategyProps
  extends SystemRouteProps<{ deploymentName: string }> {
  strategy: keyof typeof SyncStrategies;
  assetClass: keyof EntityModels;
  onChange: (newStrategy: keyof typeof SyncStrategies) => void;
  showHelp?: boolean;
}

class DeploymentTableSyncStrategy extends React.PureComponent<
  DeploymentTableSyncStrategyProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<DeploymentTableSyncStrategyProps> = {
    showHelp: false,
  };

  render() {
    const { strategy, showHelp, assetClass } = this.props;
    const deploymentName = this.props.match.params.deploymentName;
    const isItemLevelStrategy = strategy === SyncStrategies.ITEM_LEVEL_SYNC;
    const buttonColor = 'primary';
    return (
      <ButtonGroupWrapper size="sm" className="c-pointer">
        {showHelp && (
          <SyncStrategyInfo
            body={<TableSyncInfo />}
            buttonConfig={{ color: buttonColor, outline: true }}
          />
        )}
        <Button
          outline
          onClick={() => this._handleClick(SyncStrategies.NO_SYNC)}
          color={buttonColor}
          active={strategy === SyncStrategies.NO_SYNC}
        >
          <FormattedMessage {...messages.noSync} />
        </Button>

        <Button
          data-cy="deployment-deploy-table"
          outline
          onClick={() => this._handleClick(SyncStrategies.DEPLOY_TABLE)}
          color={buttonColor}
          active={strategy === SyncStrategies.DEPLOY_TABLE}
        >
          <FormattedMessage {...messages.deployTable} />
        </Button>

        <Button
          outline
          onClick={() =>
            this._handleClick(SyncStrategies.DEPLOY_AND_SYNC_TABLE)
          }
          color={buttonColor}
          active={strategy === SyncStrategies.DEPLOY_AND_SYNC_TABLE}
        >
          <FormattedMessage {...messages.syncTable} />
        </Button>

        {/* we have to mimic the button here in order to use a Link */}
        <Link
          to={detailUrls.deployments(deploymentName, assetClass)}
          className={`btn btn-outline-${buttonColor} ${
            isItemLevelStrategy ? 'c-pointer' : ''
          } ${isItemLevelStrategy ? 'active' : ''}`}
        >
          <FormattedMessage {...messages.syncItem} />
          &nbsp;
          {isItemLevelStrategy && <WrenchIcon />}
        </Link>
      </ButtonGroupWrapper>
    );
  }

  _handleClick = (strat: keyof typeof SyncStrategies) => {
    this.props.onChange(strat);
  };

  _configureItemLevelSync = () => {
    this._handleClick(SyncStrategies.ITEM_LEVEL_SYNC);
  };
}

export default withRouter(DeploymentTableSyncStrategy);



