/*
 * TableSyncInfo Messages
 *
 * This contains all the text for the TableSyncInfo component.
 */
import { defineMessages } from 'react-intl';

export default defineMessages({
  dontDeploy: {
    id: 'app.components.TableSyncInfo.dontDeploy',
    defaultMessage: "Don't Deploy",
  },
  dontDeployInfo: {
    id: 'app.components.TableSyncInfo.dontDeployInfo',
    defaultMessage:
      'By selecting this option the table will exist only on the platform, i.e., the table will not be replicated to any edges',
  },
  deployTable: {
    id: 'app.components.TableSyncInfo.deployTable',
    defaultMessage: 'Deploy Table',
  },
  deployTableInfo: {
    id: 'app.components.TableSyncInfo.deployTableInfo',
    defaultMessage:
      'By selecting this option all items within the table will be pushed to the edges defined in this deployment. Any changes made to the table or items within the table will be replicated to the defined edges.',
  },
  syncTable: {
    id: 'app.components.TableSyncInfo.syncTable',
    defaultMessage: 'Sync Table',
  },
  syncTableInfo: {
    id: 'app.components.TableSyncInfo.syncTableInfo',
    defaultMessage:
      'When a table is selected to sync with edges it will set up a bi-directional contract between the platform and edges - if said table is modified on the platform, the change will be replicated to all edges within the deployment. Conversely, if said table is modified at the edge, it will be replicated on the platform and all the other edges defined in the deployment.',
  },
  item: {
    id: 'app.components.TableSyncInfo.item',
    defaultMessage: 'Item',
  },
  itemInfo: {
    id: 'app.components.TableSyncInfo.itemInfo',
    defaultMessage:
      'This option allows you to configure a subset of items from this table that should be deployed/synced.',
  },
});



