import { createSelector } from 'reselect';
import { omit, pickBy } from 'lodash';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { selectEntities } from 'containers/Console/consoleSelectors';
import {
  AUTHENTICATED_ROLE,
  ANONYMOUS_ROLE,
  ADMINISTRATOR_ROLE,
} from 'cb-utils/console-entity-models/roles';
import { VISIBILITY } from 'cb-utils/console-entity-models/libraries';
import { EntityModels } from 'containers/Console/state';
import { AppState } from 'reducers';

/**
 * Direct selector to the DeploymentManagerModal state domain
 */
const selectDeploymentManagerModalDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

/**
 * Default selector used by DeploymentManagerModal
 */

const makeSelectDeploymentManagerModal = () =>
  createSelector(selectDeploymentManagerModalDomain(), (substate) => substate);

const selectIsSubmitting = () =>
  createSelector(
    makeSelectDeploymentManagerModal(),
    (substate) => substate.isSubmitting,
  );

const selectFilteredEntities = () =>
  createSelector(selectEntities(), (substate): EntityModels => {
    return {
      ...substate,
      roles: {
        ...substate.roles,
        entities: omit(substate.roles.entities, [
          // we don't want to show default roles since they're "baked in" to the edge
          AUTHENTICATED_ROLE,
          ANONYMOUS_ROLE,
          ADMINISTRATOR_ROLE,
        ]),
      },
      libraries: {
        ...substate.libraries,
        entities: pickBy(
          substate.libraries.entities,
          (val) => val.visibility !== VISIBILITY.GLOBAL, // we don't want to show global libraries since they're "baked in" to the edge
        ),
      },
    };
  });

export default makeSelectDeploymentManagerModal;
export {
  selectDeploymentManagerModalDomain,
  selectIsSubmitting,
  selectFilteredEntities,
};



