/*
*
* DeploymentEdgeGrid
*
*/

import React from 'react';
import { GenericDuckSlicesEnum } from 'containers/Console/state';
import GridContainer from 'containers/ConsoleCrudgridContainer/Loadable';
import { getSystemKey } from 'cb-utils/getPathParams';
import { Column } from 'react-table';
import { createCheckBoxCell } from 'cb-utils/createCheckBoxCell';
import { findIndex } from 'lodash';
import { shouldDeployToAllEdges } from 'cb-utils/console-entity-models';
import { edgeSelectors } from 'containers/Console/ducks';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';

interface DeploymentEdgeGridProps {
  columnNames: string[];
  selectedEdges: string[];
  onToggleEdges: (edges: string[]) => void;
  allowFilter?: boolean;
}

class DeploymentEdgeGrid extends React.PureComponent<
  DeploymentEdgeGridProps & StateToProps
> {
  render() {
    const { columnNames, allowFilter } = this.props;
    return (
      <GridContainer
        assetClass={GenericDuckSlicesEnum.edges}
        systemKey={getSystemKey()}
        columnNames={columnNames}
        allowFilter={allowFilter}
        transformColumns={this._transformEdgeColumns}
      />
    );
  }

  _transformEdgeColumns = (cols: Column[]): Column[] => {
    return [
      {
        Header: () => (
          <div data-cy="toggle-deploy-all">
            {createCheckBoxCell(
              this._toggleSelectAllEdges,
              this._hasSelectedAllEdges(),
            )}
          </div>
        ),
        filterable: false,
        resizable: false,
        sortable: false,
        width: 80,
        Cell: (row) => (
          <div>
            {createCheckBoxCell(
              (isChecked) => this._toggleEdge(isChecked, row.row.name),
              this._isEdgeSelected(row.row.name),
            )}
          </div>
        ),
      },
      ...cols,
    ];
  };

  _isEdgeSelected = (name: string) => {
    return this.props.selectedEdges.indexOf(name) > -1;
  };

  _toggleEdge = (isChecked: boolean, name: string) => {
    let { selectedEdges } = this.props;
    selectedEdges = shouldDeployToAllEdges(selectedEdges) ? [] : selectedEdges;
    let newSelected: string[];
    if (isChecked) {
      newSelected = [name].concat(selectedEdges);
    } else {
      newSelected = selectedEdges.filter((edge) => edge !== name);
    }
    this.props.onToggleEdges(newSelected);
  };

  _toggleSelectAllEdges = (isChecked: boolean) => {
    let { selectedEdges } = this.props;
    selectedEdges = shouldDeployToAllEdges(selectedEdges) ? [] : selectedEdges;
    let newSelected: string[];
    if (isChecked) {
      newSelected = this.props.rows
        .filter(
          // we need to filter out any rows that are already selected
          (s) => selectedEdges.indexOf(s.name) === -1,
        )
        .map((k) => k.name)
        .concat(selectedEdges); // we need to concat with what's already selected to allow for selections across pages
    } else {
      newSelected = selectedEdges.filter((s) => {
        return (
          findIndex(this.props.rows, {
            name: s,
          }) === -1
        );
      });
    }
    this.props.onToggleEdges(newSelected);
  };

  _hasSelectedAllEdges = () => {
    for (const row of this.props.rows) {
      if (this.props.selectedEdges.indexOf(row.name) === -1) {
        return false;
      }
    }
    return true;
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  rows: edgeSelectors.rows(),
});

const withConnect = connect<StateToProps, DeploymentEdgeGridProps>(
  mapStateToProps,
);

export default withConnect(DeploymentEdgeGrid);



