import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import {
  adapterSelectors,
  deviceSelectors,
  librarySelectors,
  pluginSelectors,
  portalSelectors,
  serviceSelectors,
  timerSelectors,
  triggerSelectors,
  userSelectors,
  collectionSelectors,
} from 'containers/Console/ducks';
import { selectAssetIsLoading } from 'containers/Console/consoleSelectors';
import {
  AssetConfig,
  shouldDeployAllAssetsForClass,
  shouldSyncAllAssetsForClass,
  doesAssetClassExist,
} from 'cb-utils/console-entity-models/deployment';
import {
  EntityModels,
  GenericEntityModel,
  isTableEntityModel,
} from 'containers/Console/state';
import { selectModifiableDeploymentAssets } from 'containers/Console/selectors/deployments';
import { AppState } from 'reducers';

/**
 * Direct selector to the deploymentAssetManager state domain
 */
const selectDeploymentAssetManagerDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

/**
 * Default selector used by DeploymentAssetManager
 */

const makeSelectDeploymentAssetManager = () =>
  createSelector(selectDeploymentAssetManagerDomain(), (substate) => substate);

const selectExpandAll = () =>
  createSelector(
    makeSelectDeploymentAssetManager(),
    (substate) => substate.expandAll,
  );
const selectEntitiesPresentation = () =>
  createSelector(
    makeSelectDeploymentAssetManager(),
    (substate) => substate.entitiesPresentation,
  );
const selectServicesPresentation = (deploymentName: string) =>
  createSelector(
    serviceSelectors.wholeSlice,
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    selectAssetIsLoading('services', 'FETCH_ALL'),
    (services, servicesPres, assets, isFetching) => {
      const filteredAssets = filterAssetsForClass(assets, 'services');
      return {
        ...servicesPres.services,
        isFetching,
        previewText: getPreviewText(filteredAssets, services, isFetching),
        assets: filteredAssets,
      };
    },
  );
const selectPortalsPresentation = (deploymentName: string) =>
  createSelector(
    portalSelectors.wholeSlice,
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    selectAssetIsLoading('portals', 'FETCH_ALL'),
    (portals, portalsPres, assets, isFetching) => {
      const filteredAssets = filterAssetsForClass(assets, 'portals');
      return {
        ...portalsPres.portals,
        isFetching,
        previewText: getPreviewText(filteredAssets, portals, isFetching),
        assets: filteredAssets,
      };
    },
  );
const selectLibrariesPresentation = (deploymentName: string) =>
  createSelector(
    librarySelectors.selectCustomLibraries(),
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    selectAssetIsLoading('libraries', 'FETCH_ALL'),
    (libraries, librariesPres, assets, isFetching) => {
      const filteredAssets = filterAssetsForClass(assets, 'libraries');
      return {
        ...librariesPres.libraries,
        isFetching,
        previewText: getPreviewText(filteredAssets, libraries, isFetching),
        assets: filteredAssets,
      };
    },
  );
const selectTriggersPresentation = (deploymentName: string) =>
  createSelector(
    triggerSelectors.wholeSlice,
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    selectAssetIsLoading('triggers', 'FETCH_ALL'),
    (triggers, triggersPres, assets, isFetching) => {
      const filteredAssets = filterAssetsForClass(assets, 'triggers');
      return {
        ...triggersPres.triggers,
        isFetching,
        previewText: getPreviewText(filteredAssets, triggers, isFetching),
        assets: filteredAssets,
      };
    },
  );
const selectTimersPresentation = (deploymentName: string) =>
  createSelector(
    timerSelectors.wholeSlice,
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    selectAssetIsLoading('timers', 'FETCH_ALL'),
    (timers, timersPres, assets, isFetching) => {
      const filteredAssets = filterAssetsForClass(assets, 'timers');
      return {
        ...timersPres.timers,
        isFetching,
        previewText: getPreviewText(filteredAssets, timers, isFetching),
        assets: filteredAssets,
      };
    },
  );
const selectUsersPresentation = (deploymentName: string) =>
  createSelector(
    userSelectors.wholeSlice,
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    selectAssetIsLoading('users', 'FETCH_TOTAL'),
    (users, usersPres, assets, isFetching) => {
      const filteredAssets = filterAssetsForClass(assets, 'users');
      return {
        ...usersPres.users,
        isFetching,
        previewText: getPreviewText(filteredAssets, users, isFetching),
        assets: filteredAssets,
      };
    },
  );
const selectDevicesPresentation = (deploymentName: string) =>
  createSelector(
    deviceSelectors.wholeSlice,
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    selectAssetIsLoading('devices', 'FETCH_TOTAL'),
    (devices, devicesPres, assets, isFetching) => {
      const filteredAssets = filterAssetsForClass(assets, 'devices');
      return {
        ...devicesPres.devices,
        isFetching,
        previewText: getPreviewText(filteredAssets, devices, isFetching),
        assets: filteredAssets,
      };
    },
  );
const selectCollectionsPresentation = (deploymentName: string) =>
  createSelector(
    collectionSelectors.wholeSlice,
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    (collections, collectionsPres, assets) => {
      const filteredAssets = assets.filter(
        (a) =>
          a.asset_class !== 'services' &&
          a.asset_class !== 'devices' &&
          a.asset_class !== 'users' &&
          a.asset_class !== 'adaptors' &&
          a.asset_class !== 'plugins' &&
          a.asset_class !== 'roles' &&
          a.asset_class !== 'timers' &&
          a.asset_class !== 'triggers' &&
          a.asset_class !== 'libraries' &&
          a.asset_class !== 'portals',
      );
      return {
        ...collectionsPres.collections,
        isFetching: collections.isFetching,
        previewText: getCollectionsPreviewText(filteredAssets, collections),
        assets: filteredAssets,
      };
    },
  );
const selectAdaptersPresentation = (deploymentName: string) =>
  createSelector(
    adapterSelectors.wholeSlice,
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    selectAssetIsLoading('adaptors', 'FETCH_ALL'),
    (adapters, adaptersPres, assets, isFetching) => {
      const filteredAssets = filterAssetsForClass(assets, 'adaptors');
      return {
        ...adaptersPres.adapters,
        isFetching,
        previewText: getPreviewText(filteredAssets, adapters, isFetching),
        assets: filteredAssets,
      };
    },
  );
const selectPluginsPresentation = (deploymentName: string) =>
  createSelector(
    pluginSelectors.wholeSlice,
    selectEntitiesPresentation(),
    selectModifiableDeploymentAssets(deploymentName),
    selectAssetIsLoading('plugins', 'FETCH_TOTAL'),
    (plugins, pluginsPres, assets, isFetching) => {
      const filteredAssets = filterAssetsForClass(assets, 'plugins');
      return {
        ...pluginsPres.plugins,
        isFetching,
        previewText: getPreviewText(filteredAssets, plugins, isFetching),
        assets: filteredAssets,
      };
    },
  );

const filterAssetsForClass = (
  selected: AssetConfig[],
  assetClass: keyof EntityModels,
): AssetConfig[] => {
  return selected.filter((s) => s.asset_class === assetClass);
};

const getTotal = (assets: GenericEntityModel, isFetching?: boolean): string => {
  if (isFetching) {
    return '...';
  } else if (isTableEntityModel(assets) && assets.count.total.hasFetched) {
    return `${assets.count.total.value}`;
  } else if (assets.hasFetched) {
    return `${Object.keys(assets.entities).length}`;
  }
  return 'Error';
};

const getPreviewText = (
  selected: AssetConfig[],
  assets: GenericEntityModel,
  isFetching: boolean,
) => {
  if (
    shouldDeployAllAssetsForClass(selected) ||
    shouldSyncAllAssetsForClass(selected)
  ) {
    return 'All';
  }
  return `${selected.length} of ${getTotal(assets, isFetching)}`;
};

const getCollectionsPreviewText = (
  selected: AssetConfig[],
  assets: GenericEntityModel,
) => {
  const numOfCollections = Object.keys(assets.entities).length;
  let collectionsToSync = 0;
  for (const key in assets.entities) {
    if (doesAssetClassExist(selected, key)) {
      collectionsToSync += 1;
    }
  }
  return numOfCollections === collectionsToSync
    ? 'All'
    : `${collectionsToSync} of ${getTotal(assets)}`;
};

export default makeSelectDeploymentAssetManager;
export {
  selectExpandAll,
  selectServicesPresentation,
  selectPortalsPresentation,
  selectLibrariesPresentation,
  selectTriggersPresentation,
  selectTimersPresentation,
  selectUsersPresentation,
  selectDevicesPresentation,
  selectCollectionsPresentation,
  selectAdaptersPresentation,
  selectPluginsPresentation,
};



