/**
 *
 * SimpleAssetTable
 *
 */

import React from 'react';
import styled from 'styled-components';
import { findIndex } from 'lodash';

import { Table } from 'reactstrap';

import { FormattedMessage } from 'react-intl';
import consoleMessages from 'containers/Console/messages';
import messages from './messages';
import { EntityMap, Entity } from 'containers/Console/state';
import {
  AssetConfig,
  shouldDeployAllAssetsForClass,
  shouldSyncAllAssetsForClass,
} from 'cb-utils/console-entity-models/deployment';
import SyncStrategyInfo from 'components/SyncStrategyInfo';
import Info from './syncInfo';

const TableWrapper = styled(Table)`
  text-align: left;
`;

interface SimpleAssetTableProps {
  onToggleDeploy: (key: string, isChecked: boolean) => void;
  onToggleSync: (key: string, isChecked: boolean) => void;
  onToggleDeployAll: (isChecked: boolean) => void;
  onToggleSyncAll: (isChecked: boolean) => void;
  selected: AssetConfig[];
  rows: EntityMap;
}

class SimpleAssetTable extends React.PureComponent<SimpleAssetTableProps> {
  // eslint-disable-line react/prefer-stateless-function
  render() {
    const { selected } = this.props;
    return (
      <TableWrapper striped size="sm">
        <thead>
          <tr>
            <th>
              <FormattedMessage {...messages.nameHeader} />
            </th>
            <th>
              <input
                type="checkbox"
                checked={this._shouldDeployAll(selected)}
                onChange={(e) => this.props.onToggleDeployAll(e.target.checked)}
              />
              &nbsp;
              <FormattedMessage {...consoleMessages.deploy} />
              <SyncStrategyInfo body={<Info />} />
            </th>
            <th>
              <input
                type="checkbox"
                checked={this._shouldSyncAll(selected)}
                onChange={(e) => this.props.onToggleSyncAll(e.target.checked)}
              />
              &nbsp;
              <FormattedMessage {...consoleMessages.sync} />
            </th>
          </tr>
        </thead>
        <tbody>{this._renderRows(this.props.rows)}</tbody>
      </TableWrapper>
    );
  }

  _shouldDeployAll = (selected: AssetConfig[]): boolean => {
    return shouldDeployAllAssetsForClass(selected);
  };

  _shouldSyncAll = (selected: AssetConfig[]): boolean => {
    return shouldSyncAllAssetsForClass(selected);
  };

  _isItemDeployed = (row: Entity, selected: AssetConfig[]): boolean => {
    return (
      this._shouldDeployAll(selected) ||
      findIndex(selected, { asset_id: row.name }) > -1
    );
  };

  _isItemSynced = (row: Entity, selected: AssetConfig[]): boolean => {
    return (
      this._shouldSyncAll(selected) ||
      findIndex(selected, { asset_id: row.name, sync: true }) > -1
    );
  };

  _renderRows = (rows: EntityMap) => {
    const rtn = [];
    for (const key in rows) {
      if (rows.hasOwnProperty(key)) {
        rtn.push(this._renderTableRow(rows[key]));
      }
    }
    return rtn;
  };

  _renderTableRow = (row: Entity) => {
    return (
      <tr key={row.name}>
        <td>{row.name}</td>
        <td>
          <input
            type="checkbox"
            checked={this._isItemDeployed(row, this.props.selected)}
            onChange={(e) =>
              this.props.onToggleDeploy(row.name, e.target.checked)
            }
          />
        </td>
        <td>
          <input
            type="checkbox"
            checked={this._isItemSynced(row, this.props.selected)}
            onChange={(e) =>
              this.props.onToggleSync(row.name, e.target.checked)
            }
          />
        </td>
      </tr>
    );
  };
}

export default SimpleAssetTable;



