/**
 *
 * CardWithHoverEffect
 *
 */

import React, { SFC } from 'react';
import styled from 'styled-components';

import { Card } from 'reactstrap';

const CardWrapper = styled(Card)`
  ${(props: { 'data-alloweffect': boolean }) =>
    !props['data-alloweffect'] &&
    `transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;

&::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 2s cubic-bezier(0.165, 0.84, 0.44, 1);
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.15);
  content: '';
  opacity: 0;
  z-index: -1;
}

&:hover,
&:focus {
  transform: scale3d(1.006, 1.006, 1);

  &::after {
    opacity: 1;
  }
}`};
`;

interface CardWithHoverEffectProps {
  allowEffect: boolean;
}

const CardWithHoverEffect: SFC<CardWithHoverEffectProps> = (props) => (
  <CardWrapper data-alloweffect={props.allowEffect}>
    {props.children}
  </CardWrapper>
);

export default CardWithHoverEffect;



