/*
 *
 * DeploymentAssetManager reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';
import { FormattedMessage } from 'react-intl';
import { mapValues } from 'lodash';
import messages from 'containers/Console/messages';
import { AssetConfig } from 'cb-utils/console-entity-models/deployment';
import { ActionConstants as DeployPageConstants } from 'containers/Deploy/actionConstants';
import { ActionTypes as DeployPageActionTypes } from 'containers/Deploy/actions';
import { ActionConstants as DeploymentModalConstants } from 'containers/DeployDetail/actionConstants';
import { ActionTypes as DeploymentModalActionTypes } from 'containers/DeployDetail/actions';
import { Map } from 'cb-utils/console-entity-models';

interface EntitiesPresentation extends Map<EPresentation> {
  services: EPresentation;
  portals: EPresentation;
  libraries: EPresentation;
  triggers: EPresentation;
  timers: EPresentation;
  users: EPresentation;
  roles: EPresentation;
  adapters: EPresentation;
  plugins: EPresentation;
  collections: EPresentation;
  devices: EPresentation;
}

export interface EPresentation {
  name: string;
  isExpanded: boolean;
  displayName: FormattedMessage.MessageDescriptor;
  isFetching: boolean;
  assets: AssetConfig[];
  previewText: string;
}

export interface State {
  expandAll: boolean;
  entitiesPresentation: EntitiesPresentation;
}

export const initialState: State = {
  expandAll: false,
  entitiesPresentation: {
    services: {
      name: 'services',
      displayName: messages.assetClassServices,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    portals: {
      name: 'portals',
      displayName: messages.assetClassPortals,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    libraries: {
      name: 'libraries',
      displayName: messages.assetClassLibraries,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    triggers: {
      name: 'triggers',
      displayName: messages.assetClassTriggers,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    timers: {
      name: 'timers',
      displayName: messages.assetClassTimers,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    roles: {
      name: 'roles',
      displayName: messages.assetClassRoles,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    users: {
      name: 'users',
      displayName: messages.assetClassUsers,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    devices: {
      name: 'devices',
      displayName: messages.assetClassDevices,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    collections: {
      name: 'collections',
      displayName: messages.assetClassCollections,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    adapters: {
      name: 'adapters',
      displayName: messages.assetClassAdapters,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
    plugins: {
      name: 'plugins',
      displayName: messages.assetClassPlugins,
      isExpanded: false,
      isFetching: false,
      assets: [],
      previewText: '',
    },
  },
};

const toggleIsExpanded = (c: EPresentation, val: boolean): EPresentation => {
  return {
    ...c,
    isExpanded: val,
  };
};

const toggleAll = (currentState: State, newVal: boolean): State => {
  return assign(currentState, (s) => {
    s.expandAll = newVal;
    s.entitiesPresentation = mapValues(s.entitiesPresentation, (val) =>
      toggleIsExpanded(val, s.expandAll),
    );
    return s;
  });
};

function deploymentAssetManagerReducer(
  state: State = initialState,
  action: ActionTypes | DeployPageActionTypes | DeploymentModalActionTypes,
) {
  switch (action.type) {
    case ActionConstants.TOGGLE_EXPAND_ALL:
      return toggleAll(state, !state.expandAll);
    case DeployPageConstants.ON_DISMISS_DEPLOYMENT_MODAL:
    case DeploymentModalConstants.UPDATE_DEPLOYMENT_SUCCESS:
    case DeploymentModalConstants.CREATE_DEPLOYMENT_SUCCESS:
      return toggleAll(state, false);
    case ActionConstants.TOGGLE_EXPAND_ASSET_CLASS:
      return assign(
        state,
        (s) => s.entitiesPresentation[action.payload.assetClass],
        (m) => ({
          ...m,
          isExpanded: !m.isExpanded,
        }),
      );
    default:
      return state;
  }
}

export default deploymentAssetManagerReducer;



