/*
 *
 * DeployDetail
 *
 */

import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import { selectIsSubmitting, selectFilteredEntities } from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import consoleMessages from 'containers/Console/messages';
import messages from './messages';
import { TrashIcon } from 'components/EntityIcons';

import { Form, Button, Col, Container, Row } from 'reactstrap';

import {
  createInitAssetManager,
  createEdgeToggle,
  createAssetDeployToggle,
  createAssetSyncToggle,
  createUpdateDeploymentRequest,
  createAssetDeployToggleAll,
  createAssetSyncToggleAll,
  createEdgeToggleAll,
  createAssetChange,
  createRefreshAssets,
  createDeploymentDescriptionChange,
  createDeleteDeploymentRequest,
} from './actions';
import { EntityModels, EntityMap } from 'containers/Console/state';

import DeploymentAssetManager from 'containers/DeploymentAssetManager';
import DeploymentEdgeManager from 'components/DeploymentEdgeManager';
import LoadingIndicator from 'components/LoadingIndicator';
import ConsolePage from 'components/ConsolePage';
import PageHeader from 'components/ConsolePageHeader';
import PageFooter from 'components/ConsolePageFooter';
import Helmet from 'components/DeployHelmet';
import DescEditor from 'components/EntityDescriptionEditor';

import { AssetConfig } from 'cb-utils/console-entity-models/deployment';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { selectModifiableDeploymentByName } from '../Console/selectors/deployments';
import {
  createNavToOverview,
  createShowConfirmationModal,
} from '../Console/actions/ui';
import {
  DeploymentDetailRouteProps,
  deployRoot,
} from '../Console/routes/deployments';
import { CBDispatch } from 'cb-utils/typeUtils';
import { edgeSelectors } from 'containers/Console/ducks';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';

const FormWrapper = styled(Form)`
  width: 100%;
`;

interface DeployDetailProps
  extends SystemRouteProps<DeploymentDetailRouteProps> {}

interface DeployDetailState {
  entitiesPresentation: EntitiesPresentation;
  expandAll: boolean;
}

interface EntitiesPresentation {
  services: EPresentation;
}

interface EPresentation {
  name: string;
  isExpanded: boolean;
  displayName: { id: string; defaultMessage?: string };
}

class DeployDetail extends React.Component<
  DeployDetailProps & StateToProps & DispatchToProps,
  DeployDetailState
> {
  // eslint-disable-line react/prefer-stateless-function

  systemKey = this.props.match.params.systemKey;
  deploymentName = this.props.match.params.deploymentName;

  componentWillMount() {
    this.props.init(this.systemKey, this.deploymentName);
  }

  render() {
    const {
      entities,
      onToggleEdge,
      onEdgeToggleAll,
      deployment,
      onAssetDeployToggle,
      onAssetSyncToggle,
      onAssetDeployToggleAll,
      onAssetSyncToggleAll,
      onAssetChange,
      refreshAssets,
      totalEdgeCount,
    } = this.props;
    return (
      <React.Fragment>
        <Helmet />
        {deployment ? (
          <React.Fragment>
            <ConsolePage subheader={this._renderHeader()}>
              <FormWrapper>
                <Container>
                  <Row>
                    <Col sm={12} lg={6}>
                      <DeploymentAssetManager
                        deploymentName={this.deploymentName}
                        refresh={() => refreshAssets(this.systemKey)}
                        entities={entities}
                        onDeployChange={(
                          assetClass: keyof EntityModels,
                          assetId: string,
                          isChecked: boolean,
                        ) =>
                          onAssetDeployToggle(
                            entities[assetClass].entities,
                            assetClass,
                            assetId,
                            isChecked,
                          )
                        }
                        onToggleDeployAll={onAssetDeployToggleAll}
                        onSyncChange={(
                          assetClass: keyof EntityModels,
                          assetId: string,
                          isChecked: boolean,
                        ) =>
                          onAssetSyncToggle(
                            entities[assetClass].entities,
                            assetClass,
                            assetId,
                            isChecked,
                          )
                        }
                        onToggleSyncAll={onAssetSyncToggleAll}
                        selectedAssets={deployment.assets}
                        onAssetChange={onAssetChange}
                      />
                    </Col>
                    <Col sm={12} lg={6}>
                      <DeploymentEdgeManager
                        deploymentName={this.deploymentName}
                        edges={entities.edges.entities}
                        onToggleAll={(isChecked: boolean) =>
                          onEdgeToggleAll(isChecked)
                        }
                        onToggleEdge={onToggleEdge}
                        selectedEdges={deployment.edges}
                        totalEdges={totalEdgeCount}
                      />
                    </Col>
                  </Row>
                </Container>
              </FormWrapper>
            </ConsolePage>
            <PageFooter
              leftButtons={this.renderDeleteButton}
              actionButtons={() => (
                <React.Fragment>
                  {this._renderCancelButton()}
                  {this._renderSubmitButton()}
                </React.Fragment>
              )}
            />
          </React.Fragment>
        ) : (
          <LoadingIndicator forPage />
        )}
      </React.Fragment>
    );
  }

  renderDeleteButton = () => {
    const { deleteDeployment } = this.props;
    return (
      <Button
        color="link"
        onClick={() => deleteDeployment(this.systemKey)}
        data-cy="delete-deployment"
      >
        <TrashIcon />
        &nbsp;
        <FormattedMessage {...messages.deleteDeployment} />
      </Button>
    );
  };

  _renderCancelButton = () => {
    return (
      <Button color="default" onClick={this.props.discardChanges}>
        <FormattedMessage {...consoleMessages.discardChanges} />
      </Button>
    );
  };

  _getSubmitButtonText = () => {
    return this.props.isSubmitting ? (
      <LoadingIndicator />
    ) : (
      <FormattedMessage {...consoleMessages.saveAndExit} />
    );
  };

  _renderSubmitButton = () => {
    const { deployment, isSubmitting, onSubmit } = this.props;
    return (
      <Button
        data-cy="deployment-update-deployment"
        disabled={isSubmitting || deployment.name === ''}
        color="primary"
        onClick={() => onSubmit(this.systemKey)}
      >
        {this._getSubmitButtonText()}
      </Button>
    );
  };

  _renderHeader = () => {
    return (
      <PageHeader
        header={this._renderHeaderContents()}
        subHeader={
          <DescEditor
            value={this.props.deployment.description}
            onChange={this.props.onDescriptionChange}
            defaultValue="Description"
          />
        }
      />
    );
  };

  _renderHeaderContents = () => {
    return <div>{this.deploymentName}</div>;
  };
}

type StateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapStateToProps = (state: AppState, ownProps: DeployDetailProps) =>
  createStructuredSelector({
    entities: selectFilteredEntities(),
    deployment: selectModifiableDeploymentByName(
      ownProps.match.params.deploymentName,
    ),
    isSubmitting: selectIsSubmitting(),
    totalEdgeCount: edgeSelectors.filteredCount(),
  });

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(dispatch: CBDispatch, ownProps: DeployDetailProps) {
  const depName = ownProps.match.params.deploymentName;
  return {
    init: (systemKey: string, id: string) =>
      dispatch(createInitAssetManager(systemKey, id)),
    refreshAssets: (sysKey: string) => dispatch(createRefreshAssets(sysKey)),
    onDescriptionChange: (val: string) =>
      dispatch(createDeploymentDescriptionChange(depName, val)),
    onToggleEdge: (newSelected: string[]) =>
      dispatch(createEdgeToggle(depName, newSelected)),
    onEdgeToggleAll: (isChecked: boolean) =>
      dispatch(createEdgeToggleAll(depName, isChecked)),
    onAssetDeployToggle: (
      allAssets: EntityMap,
      assetClass: keyof EntityModels,
      assetId: string,
      isChecked: boolean,
    ) =>
      dispatch(
        createAssetDeployToggle(
          depName,
          allAssets,
          assetClass,
          assetId,
          isChecked,
        ),
      ),
    onAssetDeployToggleAll: (
      assetClass: keyof EntityModels,
      isChecked: boolean,
    ) => dispatch(createAssetDeployToggleAll(depName, assetClass, isChecked)),
    onAssetSyncToggle: (
      allAssets: EntityMap,
      assetClass: keyof EntityModels,
      assetId: string,
      isChecked: boolean,
    ) =>
      dispatch(
        createAssetSyncToggle(
          depName,
          allAssets,
          assetClass,
          assetId,
          isChecked,
        ),
      ),
    onAssetSyncToggleAll: (
      assetClass: keyof EntityModels,
      isChecked: boolean,
    ) => dispatch(createAssetSyncToggleAll(depName, assetClass, isChecked)),
    onAssetChange: (assetClass: string, assets: AssetConfig[]) =>
      dispatch(createAssetChange(depName, assetClass, assets)),
    onSubmit: (systemKey: string) => {
      dispatch(createUpdateDeploymentRequest(systemKey, depName));
    },
    discardChanges: () => dispatch(createNavToOverview(deployRoot)),
    deleteDeployment: (sysKey: string) =>
      dispatch(
        createShowConfirmationModal(
          'Are you sure you want to delete this deployment?',
          createDeleteDeploymentRequest(sysKey, depName),
        ),
      ),
  };
}

const withConnect = connect<StateToProps, DispatchToProps, DeployDetailProps>(
  mapStateToProps,
  mapDispatchToProps,
);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect)(DeployDetail);



