/**
 *
 * MultiTableAssetClass
 *
 */

import React from 'react';
import styled from 'styled-components';

import { Table } from 'reactstrap';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import {
  AssetConfig,
  SyncStrategies,
  getSyncStrategy,
  createAssetsFromSyncStrategy,
} from 'cb-utils/console-entity-models/deployment';
import { EntityMap, Entity, EntityModels } from 'containers/Console/state';
import SyncStrategyInfo from 'components/SyncStrategyInfo';
import TableSyncInfo from 'components/TableSyncInfo';

const SyncStrategyHeaderWrapper = styled.th`
  button {
    border: none !important;
  }
`;

import SyncStrategy from 'components/DeploymentTableSyncStrategy';

interface MultiTableAssetClassProps {
  selected: AssetConfig[];
  rows: EntityMap;
  onChange: (collectionName: string, assets: AssetConfig[]) => void;
  showItemLevelSync: (assetClass: keyof EntityModels) => void;
}

const getAssetsForClass = (assetClass: string, assets: AssetConfig[]) => {
  return assets.filter((a) => a.asset_class === assetClass);
};

class MultiTableAssetClass extends React.PureComponent<
  MultiTableAssetClassProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function
  render() {
    return (
      <Table striped size="sm">
        <thead>
          <tr>
            <th>
              <FormattedMessage {...messages.nameHeader} />
            </th>
            <SyncStrategyHeaderWrapper>
              <FormattedMessage {...messages.strategyHeader} />
              <SyncStrategyInfo body={<TableSyncInfo />} />
            </SyncStrategyHeaderWrapper>
          </tr>
        </thead>
        <tbody>{this._renderRows(this.props.rows)}</tbody>
      </Table>
    );
  }

  _renderRows = (rows: EntityMap) => {
    const rtn = [];
    for (const key in rows) {
      if (rows.hasOwnProperty(key)) {
        rtn.push(this._renderTableRow(rows[key]));
      }
    }
    return rtn;
  };

  _renderTableRow = (row: Entity) => {
    const name = row.name;
    return (
      <tr key={name}>
        <td>{name}</td>
        <td>
          <SyncStrategy
            assetClass={name as keyof EntityModels}
            strategy={getSyncStrategy(
              getAssetsForClass(name, this.props.selected),
            )}
            onChange={(strat: keyof typeof SyncStrategies) =>
              this._handleOnChange(name, strat)
            }
          />
        </td>
      </tr>
    );
  };

  _handleOnChange = (
    collectionName: string,
    strategy: keyof typeof SyncStrategies,
  ) => {
    if (strategy === SyncStrategies.ITEM_LEVEL_SYNC) {
      this.props.showItemLevelSync(collectionName as keyof EntityModels);
    } else {
      const newAssets = createAssetsFromSyncStrategy(collectionName, strategy);
      this.props.onChange(collectionName, newAssets);
    }
  };
}

export default MultiTableAssetClass;



