/**
 *
 * DeploymentEdgeManager
 *
 */

import React from 'react';
import styled from 'styled-components';

import { FormattedMessage, injectIntl } from 'react-intl';
import messages from './messages';

import { Card, Button } from 'reactstrap';

import { EntityMap } from 'containers/Console/state';
import DeploymentCardBody from 'components/DeploymentCardBody';
import DeploymentColumn from 'components/DeploymentModalColumnContainer';
import { DeploymentEdges } from 'cb-utils/console-entity-models/deployment';
import { shouldDeployToAllEdges } from 'cb-utils/console-entity-models';
import { ExternalLinkIcon } from 'components/Icons';
import { Link } from 'react-router-dom';
import DeploymentEdgeGrid from 'components/DeploymentEdgeGrid';
import { detailUrls } from 'cb-utils/getUrls';

const FullWidthCard = styled(Card)`
  width: 100%;
`;

const HeaderWrapper = styled.div`
  display: flex;
  align-items: center;
`;

const TableContainer = styled.div`
  height: 60vh;
  overflow-y: scroll;
`;

interface DeploymentEdgeManagerProps {
  onToggleAll: (isChecked: boolean) => void;
  onToggleEdge: (newSelected: string[]) => void;
  selectedEdges: DeploymentEdges;
  edges: EntityMap;
  totalEdges: number;
  deploymentName: string;
}

class DeploymentEdgeManager extends React.PureComponent<
  DeploymentEdgeManagerProps & ReactIntl.InjectedIntlProps
> {
  render() {
    const {
      selectedEdges,
      onToggleEdge,
      onToggleAll,
      deploymentName,
      intl: { formatMessage },
    } = this.props;
    const edgeGridUrl = detailUrls.deployments(deploymentName, 'edges');
    return (
      <DeploymentColumn
        header={
          <HeaderWrapper>
            <h2>
              <FormattedMessage {...messages.header} />
            </h2>
            &nbsp;
            <Link to={edgeGridUrl}>
              <Button
                className="c-pointer"
                color="default"
                title={formatMessage(messages.expandTitle)}
              >
                {' '}
                <FormattedMessage {...messages.fullGrid} /> <ExternalLinkIcon />
              </Button>
            </Link>
            {this._renderSelectedEdgeCount()}
          </HeaderWrapper>
        }
      >
        <div>
          <input
            type="checkbox"
            id="allToggleBox"
            checked={shouldDeployToAllEdges(selectedEdges)}
            onChange={(e) => onToggleAll(e.target.checked)}
          />
          <label htmlFor="allToggleBox" className="ml-2">
            <FormattedMessage {...messages.applyToAll} />
          </label>
        </div>
        <FullWidthCard>
          <DeploymentCardBody>
            <TableContainer>
              <DeploymentEdgeGrid
                selectedEdges={selectedEdges}
                columnNames={['name']}
                onToggleEdges={onToggleEdge}
                allowFilter={false}
              />
            </TableContainer>
          </DeploymentCardBody>
        </FullWidthCard>
      </DeploymentColumn>
    );
  }

  _renderSelectedEdgeCount = () => {
    const { selectedEdges, totalEdges, intl: { formatMessage } } = this.props;
    return (
      <strong className="ml-auto">
        {shouldDeployToAllEdges(selectedEdges) ? (
          <FormattedMessage {...messages.allSelected} />
        ) : (
          formatMessage(messages.edgeCount, {
            selectedEdges: selectedEdges.length,
            totalEdges,
          })
        )}
      </strong>
    );
  };
}

export default injectIntl(DeploymentEdgeManager);



