import React, { SFC } from 'react';
import { FormattedMessage } from 'react-intl';
import messages from './messages';

const simpleInfo: SFC<{}> = () => {
  return (
    <dl>
      <dt>
        <FormattedMessage {...messages.deploy} />
      </dt>
      <dd>
        <FormattedMessage {...messages.deployInfo} />
      </dd>
      <dt>
        <FormattedMessage {...messages.sync} />
      </dt>
      <dd>
        <FormattedMessage {...messages.syncInfo} />
      </dd>
      <dt>
        <FormattedMessage {...messages.allAssets} />
      </dt>
      <dd>
        <FormattedMessage {...messages.allAssetsInfo} />
      </dd>
    </dl>
  );
};

export default simpleInfo;



