/*
 *
 * Console actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './deploymentConstants';
import { DeploymentConfig } from 'cb-utils/console-entity-models/deployment';

export interface SetModifiableDeployment extends BaseAction {
  type: ActionConstants.SET_MODIFIABLE_DEPLOYMENT;
  payload: {
    config: DeploymentConfig;
  };
}

export const createSetModifiableDeployment = (
  config: DeploymentConfig,
): SetModifiableDeployment => ({
  type: ActionConstants.SET_MODIFIABLE_DEPLOYMENT,
  payload: {
    config,
  },
});

export interface ClearModifiableDeployment extends BaseAction {
  type: ActionConstants.CLEAR_MODIFIABLE_DEPLOYMENT;
  payload: {
    name: string;
  };
}

export const createClearModifiableDeployment = (
  name: string,
): ClearModifiableDeployment => ({
  type: ActionConstants.CLEAR_MODIFIABLE_DEPLOYMENT,
  payload: {
    name,
  },
});

export interface FetchSingleDeploymentRequest extends BaseAction {
  type: ActionConstants.FETCH_SINGLE_DEPLOYMENT_REQUEST;
  payload: {
    systemKey: string;
    name: string;
  };
}

export const createFetchSingleDeploymentRequest = (
  key: string,
  name: string,
): FetchSingleDeploymentRequest => ({
  type: ActionConstants.FETCH_SINGLE_DEPLOYMENT_REQUEST,
  payload: {
    systemKey: key,
    name,
  },
});

export interface FetchSingleDeploymentSuccess extends BaseAction {
  type: ActionConstants.FETCH_SINGLE_DEPLOYMENT_SUCCESS;
  payload: {
    config: DeploymentConfig;
  };
}

export const createFetchSingleDeploymentSuccess = (
  config: DeploymentConfig,
): FetchSingleDeploymentSuccess => ({
  type: ActionConstants.FETCH_SINGLE_DEPLOYMENT_SUCCESS,
  payload: {
    config,
  },
});

export interface FetchSingleDeploymentError extends BaseAction {
  type: ActionConstants.FETCH_SINGLE_DEPLOYMENT_ERROR;
  payload: {
    error: any;
  };
}

export const createFetchSingleDeploymentError = (
  error: any,
): FetchSingleDeploymentError => ({
  type: ActionConstants.FETCH_SINGLE_DEPLOYMENT_ERROR,
  payload: {
    error,
  },
});

export type ActionTypes =
  | FetchSingleDeploymentError
  | FetchSingleDeploymentRequest
  | FetchSingleDeploymentSuccess
  | SetModifiableDeployment
  | ClearModifiableDeployment;



