import React from 'react';
import { FormattedMessage } from 'react-intl';

export const createCheckBoxCell = (
  onClick: (isChecked: boolean) => void,
  isChecked: boolean,
  text?: FormattedMessage.MessageDescriptor,
) => (
  <div className="text-center c-pointer" onClick={() => onClick(!isChecked)}>
    <input className="c-pointer" type="checkbox" checked={isChecked} />
    {text && <FormattedMessage {...text} />}
  </div>
);



