/**
 *
 * EntityDescriptionEditor
 *
 */

import React from 'react';
import styled from 'styled-components';

import { Input } from 'reactstrap';
import { PencilIcon } from 'components/EntityIcons';
import styleVars from 'cssOverrides/styleVars';

const Wrapper = styled.div`
  max-width: 50vw;
  display: flex;
`;

const PreviewWrapper = styled.div`
  max-width: 90%;
  color: ${styleVars.gray600};
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

const IconWrapper = styled.div`
  margin-left: 1rem;
  display: inline-block;
`;

interface EntityDescriptionEditorProps {
  value: string;
  defaultValue?: string;
  onChange: (newVal: string) => void;
  onBlur?: () => void;
}

interface EntityDescriptionEditorState {
  editMode: boolean;
}

class EntityDescriptionEditor extends React.Component<
  EntityDescriptionEditorProps,
  EntityDescriptionEditorState
> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<EntityDescriptionEditorProps> = {
    defaultValue: '',
  };

  state: EntityDescriptionEditorState = {
    editMode: false,
  };

  render() {
    const { value, onChange, defaultValue } = this.props;
    return (
      <Wrapper className="c-pointer" onClick={this.handleClick}>
        {this.state.editMode ? (
          <Input
            autoFocus
            type="textarea"
            value={value}
            onChange={(e) => onChange(e.target.value)}
            onBlur={() => this.toggleEditMode(false, true)}
          />
        ) : (
          <React.Fragment>
            <PreviewWrapper>{value || defaultValue}</PreviewWrapper>
            <IconWrapper>
              <PencilIcon />
            </IconWrapper>
          </React.Fragment>
        )}
      </Wrapper>
    );
  }

  handleClick = () => {
    if (!this.state.editMode) {
      this.toggleEditMode(true, false);
    }
  };

  toggleEditMode = (bool: boolean, onUpdate: boolean) => {
    if (onUpdate && this.props.onBlur) {
      this.props.onBlur();
    }
    this.setState({ ...this.state, editMode: bool });
  };
}

export default EntityDescriptionEditor;



