/**
 *
 * DeploymentAssetClass
 *
 */

import React from 'react';
import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';

import { CardBody, Collapse } from 'reactstrap';

import { EntityModels, GenericEntityModel } from 'containers/Console/state';
import { AssetConfig } from 'cb-utils/console-entity-models/deployment';
import SimpleAssetTable from 'components/SimpleAssetTable';
import DeploymentCardBody from 'components/DeploymentCardBody';
import SingleTableAssetClass from 'components/SingleTableAssetClass';
import HoverCard from 'components/CardWithHoverEffect';
import IconWrapper from 'components/AssetClassIconWrapper';
import MultiTableAssetClass from 'components/MultiTableAssetClass';

const CardBodyContainer = styled.div`
  cursor: pointer;
  height: 150px;
  display: flex;
`;

const MyCardBody = styled(CardBody)`
  width: 100%;
`;

const AssetClassHeader = styled.h5`
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: auto;
`;

interface DeploymentAssetClassProps {
  onToggleDeploy: (key: string, isChecked: boolean) => void;
  onToggleDeployAll: (isChecked: boolean) => void;
  onToggleSync: (key: string, isChecked: boolean) => void;
  onToggleSyncAll: (isChecked: boolean) => void;
  onExpand: () => void;
  isFetching: boolean;
  isExpanded: boolean;
  name: FormattedMessage.MessageDescriptor;
  previewText: string;
  assets: GenericEntityModel;
  selectedAssets: AssetConfig[];
  assetComponent:
    | typeof SimpleAssetTable
    | typeof SingleTableAssetClass
    | typeof MultiTableAssetClass;
  assetClassIcon: React.ComponentClass;
  onAssetChange: (assetClass: string, newAssets: AssetConfig[]) => void;
  assetClass: keyof EntityModels;
}

class DeploymentAssetClass extends React.PureComponent<
  DeploymentAssetClassProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function
  render() {
    const {
      isExpanded,
      name,
      previewText,
      assets,
      isFetching,
      selectedAssets,
      onToggleDeploy,
      onToggleSync,
      onToggleDeployAll,
      onToggleSyncAll,
      assetComponent: AssetTable,
      assetClassIcon: AssetClassIcon,
      onAssetChange,
      assetClass,
    } = this.props;
    return (
      <HoverCard allowEffect={isExpanded}>
        <CardBodyContainer onClick={this.props.onExpand}>
          <MyCardBody className="text-center">
            <IconWrapper isFetching={isFetching}>
              <AssetClassIcon />
            </IconWrapper>
            <AssetClassHeader>
              <FormattedMessage {...name} />
            </AssetClassHeader>
            <div>
              <sub>{previewText}</sub>
            </div>
          </MyCardBody>
        </CardBodyContainer>
        <Collapse isOpen={isExpanded}>
          <DeploymentCardBody>
            <AssetTable
              rows={assets.entities}
              onToggleDeploy={onToggleDeploy}
              onToggleSync={onToggleSync}
              onToggleDeployAll={onToggleDeployAll}
              onToggleSyncAll={onToggleSyncAll}
              selected={selectedAssets}
              onChange={onAssetChange}
              assetClass={assetClass}
            />
          </DeploymentCardBody>
        </Collapse>
      </HoverCard>
    );
  }
}

export default DeploymentAssetClass;



