/**
 *
 * SyncStrategyInfo
 *
 */

import React from 'react';
import styled from 'styled-components';

import { Button } from 'reactstrap';

import messages from './messages';

import HelpMe from 'components/HelpPrimitive';

const ButtonWrapper = styled(Button)`
  border: none;
`;

interface SyncStrategyInfoProps {
  body: JSX.Element;
  buttonConfig?: {};
}

class SyncStrategyInfo extends React.PureComponent<SyncStrategyInfoProps, {}> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<SyncStrategyInfoProps> = {
    buttonConfig: {
      color: 'default',
    },
  };

  render() {
    const { buttonConfig, body } = this.props;
    return (
      <HelpMe
        body={body}
        headerText={messages.syncStrategy}
        targetWrapperTag={ButtonWrapper}
        targetWrapperProps={buttonConfig}
      />
    );
  }
}

export default SyncStrategyInfo;



