/*
 * SimpleAssetTable Messages
 *
 * This contains all the text for the SimpleAssetTable component.
 */
import { defineMessages } from 'react-intl';

export default defineMessages({
  nameHeader: {
    id: 'app.components.SimpleAssetTable.nameHeader',
    defaultMessage: 'Name',
  },
  deploy: {
    id: 'app.components.SimpleAssetTable.deploy',
    defaultMessage: 'Deploy',
  },
  deployInfo: {
    id: 'app.components.SimpleAssetTable.deployInfo',
    defaultMessage:
      'When an asset is selected to deploy it will be sent to all edges defined in this deployment on either edge start up or when the deployment is created, whichever comes first. Note that once the asset has been deployed any updates made from the platform will not be replicated on the edges.',
  },
  sync: {
    id: 'app.components.SimpleAssetTable.sync',
    defaultMessage: 'Sync',
  },
  syncInfo: {
    id: 'app.components.SimpleAssetTable.syncInfo',
    defaultMessage:
      'When an asset is selected to sync with edges it will set up a bi-directional contract between the platform and edges - if said asset is modified on the platform, the change will be replicated to all edges within the deployment. Conversely, if said asset is modified at the edge, it will be replicated on the platform and all the other edges defined in the deployment.',
  },
  allAssets: {
    id: 'app.components.SimpleAssetTable.allAssets',
    defaultMessage: 'All Assets',
  },
  allAssetsInfo: {
    id: 'app.components.SimpleAssetTable.allAssetsInfo',
    defaultMessage:
      'By selecting the checkbox on top for either deploy or sync, the platform will apply the strategy to all current assets and any assets created in the future for this asset class.',
  },
});



