/**
 *
 * DeploymentAssetClassIconWrapper
 *
 */

import React from 'react';
import styled from 'styled-components';
import withStyledProps from 'cb-utils/withStyledProps';

const IconWrapper = withStyledProps<DeploymentAssetClassIconWrapperState>()(
  styled.div,
)`
  font-size: 2.5rem;
  opacity: ${(props) => (props.isOn ? '1' : '.5')};
  transition: opacity .3s ease-in-out;
  -moz-transition: opacity .3s ease-in-out;
  -webkit-transition: opacity .3s ease-in-out;
`;

interface DeploymentAssetClassIconWrapperProps {
  isFetching: boolean;
}

interface DeploymentAssetClassIconWrapperState {
  isOn: boolean;
}

class DeploymentAssetClassIconWrapper extends React.Component<
  DeploymentAssetClassIconWrapperProps,
  DeploymentAssetClassIconWrapperState
> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<DeploymentAssetClassIconWrapperProps> = {
    isFetching: false,
  };
  state = {
    isOn: true,
  };
  handle: NodeJS.Timer = null;

  componentDidMount() {
    if (this.props.isFetching) {
      this.createInterval();
    }
  }

  componentWillReceiveProps(nextProps: DeploymentAssetClassIconWrapperProps) {
    if (this.props.isFetching !== nextProps.isFetching) {
      if (nextProps.isFetching) {
        this.createInterval();
      } else {
        if (!this.state.isOn) {
          this.flipIt();
        }
        clearInterval(this.handle);
      }
    }
  }

  render() {
    return (
      <IconWrapper isOn={this.state.isOn}>{this.props.children}</IconWrapper>
    );
  }

  createInterval = () => {
    this.handle = setInterval(this.flipIt, 300);
  };

  flipIt = () => {
    this.setState({
      ...this.state,
      isOn: !this.state.isOn,
    });
  };
}

export default DeploymentAssetClassIconWrapper;



