/**
 *
 * DeploymentModalColumnContainer
 *
 */

import React, { SFC } from 'react';
// import styled from 'styled-components';

import { Container, Row } from 'reactstrap';

interface DeploymentModalColumnContainerProps {
  header: React.ReactNode;
}

const DeploymentModalColumnContainer: SFC<
  DeploymentModalColumnContainerProps
> = (props) => {
  return (
    <div>
      <div>{props.header}</div>
      <Container>
        <Row>{props.children}</Row>
      </Container>
    </div>
  );
};

export default DeploymentModalColumnContainer;



