import { takeEvery, put, call, select } from 'redux-saga/effects';
import { ActionConstants as DeploymentConstants } from 'containers/Console/actions/deploymentConstants';
import {
  FetchSingleDeploymentSuccess,
  createSetModifiableDeployment,
  FetchSingleDeploymentRequest,
  createFetchSingleDeploymentSuccess,
  createFetchSingleDeploymentError,
  createFetchSingleDeploymentRequest,
} from 'containers/Console/actions/deployment';
import { fetchSingleDeployment } from 'cb-utils/services/deployment';
import { createNavToOverview } from '../actions/ui';
import { selectModifiableDeploymentByName } from '../selectors/deployments';
import { deployRoot } from '../routes/deployments';
import { SagaIterator } from 'redux-saga';

export function* setModifiableDeploymentSaga() {
  yield takeEvery(
    DeploymentConstants.FETCH_SINGLE_DEPLOYMENT_SUCCESS,
    function*(action: FetchSingleDeploymentSuccess) {
      yield put(createSetModifiableDeployment(action.payload.config));
    },
  );
}

export function* fetchSingleDeploymentSaga() {
  yield takeEvery(
    DeploymentConstants.FETCH_SINGLE_DEPLOYMENT_REQUEST,
    function*(action: FetchSingleDeploymentRequest) {
      try {
        const config = yield call(
          fetchSingleDeployment,
          action.payload.systemKey,
          action.payload.name,
        );
        yield put(createFetchSingleDeploymentSuccess(config));
      } catch (e) {
        yield put(createFetchSingleDeploymentError(e));
      }
    },
  );
}

export function* fetchSingleDeploymentErrorSaga() {
  yield takeEvery(
    DeploymentConstants.FETCH_SINGLE_DEPLOYMENT_ERROR,
    function*() {
      yield goToDeployOverview();
    },
  );
}

export function* fetchDeploymentIfNecessary(
  systemKey: string,
  deploymentId: string,
): SagaIterator {
  const currentDeployment = yield select(
    selectModifiableDeploymentByName(deploymentId),
  );
  if (!currentDeployment) {
    yield put(createFetchSingleDeploymentRequest(systemKey, deploymentId));
    return null;
  }
  return currentDeployment;
}

export function* goToDeployOverview() {
  yield put(createNavToOverview(deployRoot));
}



