import { createSelector } from 'reselect';
import { selectModifiableEntities, selectEntities } from '../consoleSelectors';
import { Entities, DeploymentConfig } from 'cb-utils/console-entity-models';

const selectDeployments = () =>
  createSelector(selectEntities(), (substate) => substate.deployments);

const selectDeploymentByName = (name: string) =>
  createSelector(
    selectDeployments(),
    (substate: Entities<DeploymentConfig>) => substate.entities[name],
  );

const selectModifiableDeployments = () =>
  createSelector(
    selectModifiableEntities(),
    (substate) => substate.deployments,
  );

const selectModifiableDeploymentByName = (name: string) =>
  createSelector(selectModifiableDeployments(), (substate) => substate[name]);

const selectModifiableDeploymentAssets = (name: string) =>
  createSelector(
    selectModifiableDeploymentByName(name),
    (substate) => (substate ? substate.assets : []),
  );

const selectModifiableDeploymentEdges = (name: string) => {
  return createSelector(
    selectModifiableDeploymentByName(name),
    (substate) => substate.edges,
  );
};

export {
  selectDeploymentByName,
  selectModifiableDeploymentByName,
  selectModifiableDeploymentAssets,
  selectModifiableDeploymentEdges,
};



