/**
 *
 * SingleTableAssetClass
 *
 */

import React from 'react';
// import styled from 'styled-components';

import { Table } from 'reactstrap';

import SyncStrategy from 'components/DeploymentTableSyncStrategy';
import {
  AssetConfig,
  SyncStrategies,
  getSyncStrategy,
  createAssetsFromSyncStrategy,
} from 'cb-utils/console-entity-models/deployment';
import { EntityModels } from 'containers/Console/state';

interface SingleTableAssetClassProps {
  selected: AssetConfig[];
  onChange: (assetClass: keyof EntityModels, newAssets: AssetConfig[]) => void;
  showItemLevelSync: (assetClass: keyof EntityModels) => void;
  assetClass: keyof EntityModels;
}

class SingleTableAssetClass extends React.PureComponent<
  SingleTableAssetClassProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function
  render() {
    const { selected, assetClass } = this.props;
    return (
      <Table striped size="sm">
        <thead>
          <tr>
            <th>
              <SyncStrategy
                assetClass={assetClass}
                onChange={this._handleOnChange}
                strategy={getSyncStrategy(selected)}
                showHelp
              />
            </th>
          </tr>
        </thead>
      </Table>
    );
  }

  _handleOnChange = (strategy: keyof typeof SyncStrategies) => {
    const assetClass = this.props.assetClass;
    if (strategy !== SyncStrategies.ITEM_LEVEL_SYNC) {
      const newAssets = createAssetsFromSyncStrategy(assetClass, strategy);
      this.props.onChange(assetClass, newAssets);
    }
  };
}

export default SingleTableAssetClass;



