import { all, takeEvery, put, select, call } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import { selectEntities } from 'containers/Console/consoleSelectors';
import {
  InitAssetManager,
  UpdateDeploymentRequest,
  createUpdateDeploymentError,
  createUpdateDeploymentSuccess,
  RefreshAssets,
  DeleteDeploymentRequest,
  createDeleteDeploymentSuccess,
  createDeleteDeploymentError,
  UpdateDeploymentSuccess,
} from './actions';
import { EntityModels } from 'containers/Console/state';
import {
  updateDeployment,
  deleteDeployment,
} from 'cb-utils/services/deployment';
import { DeploymentConfig } from 'cb-utils/console-entity-models/deployment';
import { createBodyForUpdatingDeployment } from './utils';
import {
  triggerActions,
  libraryActions,
  collectionActions,
  timerActions,
  adapterActions,
  pluginActions,
  userActions,
  deviceActions,
  serviceActions,
  portalActions,
} from 'containers/Console/ducks';
import { createCountQuery } from 'cb-utils/console-entity-models/query';
import {
  selectDeploymentByName,
  selectModifiableDeploymentByName,
} from '../Console/selectors/deployments';
import {
  setModifiableDeploymentSaga,
  fetchSingleDeploymentSaga,
  goToDeployOverview,
  fetchSingleDeploymentErrorSaga,
  fetchDeploymentIfNecessary,
} from '../Console/sagas/deployments';
import { createClearModifiableDeployment } from '../Console/actions/deployment';

function* fetchAssets(sysKey: string, isRefreshing = false) {
  const entities: EntityModels = yield select(selectEntities());
  if (!entities.services.hasFetched || isRefreshing) {
    yield put(serviceActions.fetchAll.request(sysKey));
  }
  if (!entities.portals.hasFetched || isRefreshing) {
    yield put(portalActions.fetchAll.request(sysKey));
  }
  if (!entities.libraries.hasFetched || isRefreshing) {
    yield put(libraryActions.fetchAll.request(sysKey));
  }
  if (!entities.triggers.hasFetched || isRefreshing) {
    yield put(triggerActions.fetchAll.request(sysKey));
  }
  if (!entities.timers.hasFetched || isRefreshing) {
    yield put(timerActions.fetchAll.request(sysKey));
  }
  if (!entities.adaptors.hasFetched || isRefreshing) {
    yield put(adapterActions.fetchAll.request(sysKey));
  }
  if (!entities.plugins.hasFetched || isRefreshing) {
    yield put(pluginActions.fetchAll.request(sysKey));
  }
  if (!entities.collections.hasFetched || isRefreshing) {
    yield put(collectionActions.fetchAll.request(sysKey));
  }
  if (!entities.users.count.total.hasFetched || isRefreshing) {
    yield put(
      userActions.fetchCount.request({
        systemKey: sysKey,
        query: createCountQuery(),
      }),
    );
  }
  if (!entities.devices.count.total.hasFetched || isRefreshing) {
    yield put(
      deviceActions.fetchCount.request({
        systemKey: sysKey,
        query: createCountQuery(),
      }),
    );
  }
}

export function* initAssetManagerSaga() {
  yield takeEvery(ActionConstants.INIT_ASSET_MANAGER, function*(
    action: InitAssetManager,
  ) {
    const sysKey = action.payload.systemKey;

    yield fetchDeploymentIfNecessary(
      action.payload.systemKey,
      action.payload.deploymentId,
    );
    yield fetchAssets(sysKey, true);
  });
}

export function* updateDeploymentSaga() {
  yield takeEvery(ActionConstants.UPDATE_DEPLOYMENT_REQUEST, function*(
    action: UpdateDeploymentRequest,
  ) {
    const depName = action.payload.name;
    const existingConfig: DeploymentConfig = yield select(
      selectDeploymentByName(depName),
    );
    const newConfig: DeploymentConfig = yield select(
      selectModifiableDeploymentByName(depName),
    );
    try {
      const body = createBodyForUpdatingDeployment(existingConfig, newConfig);

      yield call(
        updateDeployment,
        action.payload.systemKey,
        newConfig.name,
        body,
      );
      yield put(createUpdateDeploymentSuccess(newConfig.name));
      yield goToDeployOverview();
    } catch (e) {
      yield put(createUpdateDeploymentError(e));
    }
  });
}

export function* refreshAssetsSaga() {
  yield takeEvery(ActionConstants.REFRESH_ASSETS, function*(
    action: RefreshAssets,
  ) {
    yield fetchAssets(action.payload.systemKey, true);
  });
}

export function* deleteDeploymentSaga() {
  yield takeEvery(ActionConstants.DELETE_DEPLOYMENT_REQUEST, function*(
    action: DeleteDeploymentRequest,
  ) {
    try {
      yield call(
        deleteDeployment,
        action.payload.systemKey,
        action.payload.name,
      );
      yield put(
        createDeleteDeploymentSuccess(
          action.payload.systemKey,
          action.payload.name,
        ),
      );
      yield goToDeployOverview();
    } catch (e) {
      yield put(createDeleteDeploymentError(e));
    }
  });
}

function* clearModifiableDeploymentSaga() {
  yield takeEvery(ActionConstants.UPDATE_DEPLOYMENT_SUCCESS, function*(
    action: UpdateDeploymentSuccess,
  ) {
    yield put(createClearModifiableDeployment(action.payload.name));
  });
}

// All sagas to be loaded
export default function*() {
  yield all([
    initAssetManagerSaga(),
    fetchSingleDeploymentSaga(),
    updateDeploymentSaga(),
    refreshAssetsSaga(),
    setModifiableDeploymentSaga(),
    deleteDeploymentSaga(),
    fetchSingleDeploymentErrorSaga(),
    clearModifiableDeploymentSaga(),
  ]);
}



