/**
 *
 * DeploymentAssetManager
 *
 */

import React from 'react';
import styled from 'styled-components';

import { compose } from 'redux';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import messages from './messages';

import { Col, Button, Container, Row } from 'reactstrap';
import { EntityModels, GenericEntityModel } from 'containers/Console/state';
import { AssetConfig } from 'cb-utils/console-entity-models/deployment';

import DeploymentAssetClass from 'components/DeploymentAssetClass';
import SimpleAssetTable from 'components/SimpleAssetTable';
import DeploymentModalColumn from 'components/DeploymentModalColumnContainer';
import SingleTableAssetClass from 'components/SingleTableAssetClass';
import MultiTableAssetClass from 'components/MultiTableAssetClass';
import { STORE_KEY } from './constants';
import reducer, { EPresentation } from './reducer';

import {
  PortalIcon,
  AdapterIcon,
  DatabaseIcon,
  CodeIcon,
  LibraryIcon,
  TriggerIcon,
  TimerIcon,
  UserIcon,
  DevicesIcon,
  PluginIcon,
} from 'components/EntityIcons';
import RefreshIcon from 'components/RefreshIcon';
import {
  selectServicesPresentation,
  selectPortalsPresentation,
  selectLibrariesPresentation,
  selectTriggersPresentation,
  selectTimersPresentation,
  selectDevicesPresentation,
  selectUsersPresentation,
  selectCollectionsPresentation,
  selectPluginsPresentation,
  selectAdaptersPresentation,
  selectExpandAll,
} from 'containers/DeploymentAssetManager/selectors';
import injectReducer from 'utils/injectReducer';
import {
  createToggleExpandAll,
  createToggleExpandAssetClass,
} from 'containers/DeploymentAssetManager/actions';
import { CBDispatch } from 'cb-utils/typeUtils';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';

const ColWrapper = styled(Col)`
  padding-bottom: 1rem;
`;

interface DeploymentAssetManagerProps {
  deploymentName: string;
  entities: EntityModels;
  onDeployChange: (
    assetClass: keyof EntityModels,
    assetId: string,
    isChecked: boolean,
  ) => void;
  onToggleDeployAll: (
    assetClass: keyof EntityModels,
    isChecked: boolean,
  ) => void;
  onSyncChange: (
    assetClass: keyof EntityModels,
    assetId: string,
    isChecked: boolean,
  ) => void;
  onToggleSyncAll: (assetClass: keyof EntityModels, isChecked: boolean) => void;
  selectedAssets: AssetConfig[];
  onAssetChange: (assetClass: string, assets: AssetConfig[]) => void;
  refresh: () => void;
}

// todo: move this to a container so that we can maintain the "isExpanded" state
// this will also keep our DeploymentManagerModal from passing down so many props
class DeploymentAssetManager extends React.PureComponent<
  DeploymentAssetManagerProps & StateToProps & DispatchToProps,
  {}
> {
  render() {
    const {
      expandAll,
      toggleExpandAll,
      devicesPresentation,
      servicesPresentation,
      portalsPresentation,
      pluginsPresentation,
      usersPresentation,
      collectionsPresentation,
      librariesPresentation,
      triggersPresentation,
      timersPresentation,
      adaptersPresentation,
    } = this.props;
    return (
      <DeploymentModalColumn
        header={
          <Container>
            <Row>
              <Col xs="10">
                <h2>
                  <FormattedMessage {...messages.header} />
                  &nbsp;
                  <Button
                    className="c-pointer"
                    color="default"
                    onClick={this.props.refresh}
                  >
                    {' '}
                    <RefreshIcon />{' '}
                  </Button>
                </h2>
              </Col>
              <Col xs="2">
                <Button
                  className="float-right"
                  color="default"
                  onClick={toggleExpandAll}
                >
                  {expandAll ? (
                    <FormattedMessage {...messages.collapseAllAssetClasses} />
                  ) : (
                    <FormattedMessage {...messages.expandAllAssetClasses} />
                  )}
                </Button>
              </Col>
            </Row>
          </Container>
        }
      >
        {this._renderAssetClass(
          devicesPresentation,
          this.props.entities.devices,
          'devices',
          SingleTableAssetClass,
          DevicesIcon,
        )}
        {this._renderAssetClass(
          adaptersPresentation,
          this.props.entities.adaptors,
          'adaptors',
          SimpleAssetTable,
          AdapterIcon,
        )}
        {this._renderAssetClass(
          servicesPresentation,
          this.props.entities.services,
          'services',
          SimpleAssetTable,
          CodeIcon,
        )}
        {this._renderAssetClass(
          librariesPresentation,
          this.props.entities.libraries,
          'libraries',
          SimpleAssetTable,
          LibraryIcon,
        )}
        {this._renderAssetClass(
          collectionsPresentation,
          this.props.entities.collections,
          'collections',
          MultiTableAssetClass,
          DatabaseIcon,
        )}
        {this._renderAssetClass(
          usersPresentation,
          this.props.entities.users,
          'users',
          SingleTableAssetClass,
          UserIcon,
        )}
        {this._renderAssetClass(
          triggersPresentation,
          this.props.entities.triggers,
          'triggers',
          SimpleAssetTable,
          TriggerIcon,
        )}
        {this._renderAssetClass(
          timersPresentation,
          this.props.entities.timers,
          'timers',
          SimpleAssetTable,
          TimerIcon,
        )}
        {this._renderAssetClass(
          portalsPresentation,
          this.props.entities.portals,
          'portals',
          SimpleAssetTable,
          PortalIcon,
        )}
        {this._renderAssetClass(
          pluginsPresentation,
          this.props.entities.plugins,
          'plugins',
          SimpleAssetTable,
          PluginIcon,
        )}
      </DeploymentModalColumn>
    );
  }

  _renderAssetClass = (
    ep: EPresentation,
    ent: GenericEntityModel,
    entityKey: keyof EntityModels,
    comp:
      | typeof SimpleAssetTable
      | typeof SingleTableAssetClass
      | typeof MultiTableAssetClass,
    assetClassIcon: React.ComponentClass,
  ) => {
    // todo: type comp param
    return (
      <ColWrapper
        key={entityKey}
        xs={12}
        sm={12}
        lg={ep.isExpanded ? 12 : 6}
        xl={ep.isExpanded ? 12 : 4}
        data-cy="deployment-asset-class"
      >
        <DeploymentAssetClass
          name={ep.displayName}
          assetClass={entityKey}
          assetClassIcon={assetClassIcon}
          previewText={ep.previewText}
          isFetching={ep.isFetching}
          isExpanded={ep.isExpanded}
          assets={ent}
          selectedAssets={ep.assets}
          onExpand={() => this.props.toggleAssetClass(ep.name)}
          assetComponent={comp}
          onToggleDeploy={(entityName: string, isChecked: boolean) =>
            this.props.onDeployChange(entityKey, entityName, isChecked)
          }
          onToggleDeployAll={(isChecked: boolean) =>
            this.props.onToggleDeployAll(entityKey, isChecked)
          }
          onToggleSync={(entityName: string, isChecked: boolean) =>
            this.props.onSyncChange(entityKey, entityName, isChecked)
          }
          onToggleSyncAll={(isChecked: boolean) =>
            this.props.onToggleSyncAll(entityKey, isChecked)
          }
          onAssetChange={(assetClass: string, assets: AssetConfig[]) =>
            this.props.onAssetChange(assetClass, assets)
          }
        />
      </ColWrapper>
    );
  };
}

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(dispatch: CBDispatch) {
  return {
    toggleExpandAll: () => dispatch(createToggleExpandAll()),
    toggleAssetClass: (assetClass: string) =>
      dispatch(createToggleExpandAssetClass(assetClass)),
  };
}

type StateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapStateToProps = (
  state: AppState,
  ownProps: DeploymentAssetManagerProps,
) => {
  const depName = ownProps.deploymentName;
  return createStructuredSelector({
    expandAll: selectExpandAll(),
    servicesPresentation: selectServicesPresentation(depName),
    portalsPresentation: selectPortalsPresentation(depName),
    librariesPresentation: selectLibrariesPresentation(depName),
    triggersPresentation: selectTriggersPresentation(depName),
    timersPresentation: selectTimersPresentation(depName),
    usersPresentation: selectUsersPresentation(depName),
    devicesPresentation: selectDevicesPresentation(depName),
    collectionsPresentation: selectCollectionsPresentation(depName),
    adaptersPresentation: selectAdaptersPresentation(depName),
    pluginsPresentation: selectPluginsPresentation(depName),
  });
};

const withConnect = connect<StateToProps, DispatchToProps>(
  mapStateToProps,
  mapDispatchToProps,
);

const withReducer = injectReducer({ key: STORE_KEY, reducer });

export default compose(withReducer, withConnect)(
  DeploymentAssetManager,
) as React.ComponentType<DeploymentAssetManagerProps>;



