import { differenceBy, find } from 'lodash';

import {
  DeploymentConfig,
  DeploymentUpdateBody,
  DeploymentEdges,
  AssetConfig,
} from 'cb-utils/console-entity-models/deployment';

// what's the algorithm here?
// we want to return an array of assetConfigs if either of the following are met:
// an asset is no longer in the original array
// an asset is still in the original array but the 'sync' property has been changed
export const compareAssets = (
  compareTo: AssetConfig[],
  comp: AssetConfig[],
): AssetConfig[] => {
  return compareTo.filter((c) => {
    const elem = find(
      // filter item if it's no longer in the original array
      comp,
      (co) => c.asset_class === co.asset_class && c.asset_id === co.asset_id,
    );
    if (!elem) {
      return true;
    } else if (elem.sync !== c.sync) {
      return true;
    }
    return false;
  });
};

export const compareEdges = (
  compareTo: DeploymentEdges,
  comp: DeploymentEdges,
): DeploymentEdges => {
  return differenceBy(compareTo, comp);
};

export const createBodyForUpdatingDeployment = (
  existingConfig: DeploymentConfig,
  newConfig: DeploymentConfig,
): DeploymentUpdateBody => {
  const body: DeploymentUpdateBody = {
    assets: {
      add: [],
      remove: [],
    },
    edges: {
      add: [],
      remove: [],
    },
  };
  // rectify the changes for assets
  // if asset didn't exist in old config, add it
  body.assets.add = compareAssets(newConfig.assets, existingConfig.assets);
  // if asset existed in old config and doesn't exist in newConfig, remove it
  body.assets.remove = compareAssets(existingConfig.assets, newConfig.assets);

  // rectify the changes for edges
  // if edge didn't exist in old config, add it
  body.edges.add = compareEdges(newConfig.edges, existingConfig.edges);
  // if edge existed in old config and doesn't exist in newConfig, remove it
  body.edges.remove = compareEdges(existingConfig.edges, newConfig.edges);

  // if the description changed, add that to the payload
  if (newConfig.description !== existingConfig.description) {
    body.description = newConfig.description;
  }
  return body;
};



