/*
 *
 * DeploymentAssetManager reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';
import { ActionTypes as DeploymentActionTypes } from 'containers/Console/actions/deployment';

export interface State {
  isSubmitting: boolean;
}

export const initialState = {
  isSubmitting: false,
};

function deploymentAssetManagerReducer(
  state: State = initialState,
  action: ActionTypes | DeploymentActionTypes,
) {
  switch (action.type) {
    case ActionConstants.UPDATE_DEPLOYMENT_REQUEST:
      return assign(state, (s) => s.isSubmitting, () => true);
    case ActionConstants.UPDATE_DEPLOYMENT_SUCCESS:
    case ActionConstants.UPDATE_DEPLOYMENT_ERROR:
      return assign(state, (s) => s.isSubmitting, () => false);
    default:
      return state;
  }
}

export default deploymentAssetManagerReducer;



